import { USER_LOGIN, USER_LOGIN_SUCCESS, USER_HAS_LOGIN, USER_LOGOUT_SUCCESS } from "../actions/types";

export default function userReducer(state = {token: null, loading: false}, action){
    switch (action.type) {
      case USER_LOGIN:
        return {
          ...state,
          loading: true
        }
      case USER_HAS_LOGIN:
        localStorage.setItem('token', action.payload);
        return {
          loading: false,
          token: action.payload
        }
      case USER_LOGIN_SUCCESS:
        localStorage.setItem('token', action.payload.key);
        window.location.reload();
        return {
          loading: false,
          token: action.payload.key
        }
      case USER_LOGOUT_SUCCESS:
        return {
          loading: false,
          token: null
        }
      default:
        return state
    }
}



