import React, { Component } from 'react';
import { deleteList, fetchLists } from '../actions/listActions';
import { fetchItems } from '../actions/itemActions';
import { connect } from 'react-redux';
import { LISTS_URL, ITEMS_URL } from '../listrUrls';
import PropTypes from 'prop-types';
import ListDetail from './ListDetail';

class Lists extends Component {

  constructor(props){
    super(props);
    this.state = {
      currentList: {},
      modalOpen: false,
    }
    this.removeList = this.removeList.bind(this);
    this.showItems = this.showItems.bind(this);
    this.toggleDetails = this.toggleDetails.bind(this);
  }

  showItems(e, data){
    this.setState({currentList: data});
    this.props.fetchItems(ITEMS_URL, data.static_id, this.props.token);
    if(!this.state.modalOpen){
      this.toggleDetails();
    }
  }

  removeList(e, listId){
    if(this.props.token){
      this.props.deleteList(LISTS_URL, listId, this.props.token);
    }
  }

  toggleDetails(){
    this.setState((prevState) => ({modalOpen: !prevState.modalOpen}));
  }

  render () {
    var hideRemove;
    var detailModal;
    const cardStyle = {
      display: 'inline-block',
      marginTop: '10px',
      marginRight: '10px',
      minWidth: '250px'
    }
    var titleStyle = {
      display: 'inline-block',
      marginRight: '10px',
    }
    if(this.props.listType === 'shared'){
      hideRemove = {
        display: 'none',
      }
    }
    detailModal = <ListDetail isOpen={this.state.modalOpen} toggle={this.toggleDetails} data={this.state.currentList} />
    const data = this.props.data.map(list => (
      <div class="card bg-light" style={cardStyle}>
        <div class="card-body">
          <h4 class="card-title" style={titleStyle}>{list.name}</h4>
          <button type="button" class="btn btn-link text-right" onClick={(e) => this.showItems(e, list)}><i class="fa fa-ellipsis-v"></i></button>
          <button type="button" class="btn btn-link text-right" style={hideRemove} onClick={e => this.removeList(e, list.static_id)}><i class="fa fa-times text-danger"></i></button>
          <h5 class="card-subtitle">{list.owner}</h5>
        </div>
      </div>
    ));
    return(
      <div>
        {data}
        {detailModal}
      </div>
    );
  }
}

Lists.proptypes = {
  deleteList: PropTypes.func.isRequired,
  fetchLists: PropTypes.func.isRequired,
  fetchItems: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
  items: state.items.items,
  itemsLoading: state.items.loading
});

export default connect(mapStateToProps, { deleteList, fetchLists, fetchItems })(Lists);



