import { USER_LOGIN, USER_LOGIN_SUCCESS, USER_HAS_LOGIN, USER_LOGOUT_SUCCESS } from './types';
import Cookies from 'js-cookie';


export const beginUserLogin = () => dispatch =>{
  return{
    type: USER_LOGIN
  }
}

export const setUserToken = (token) => dispatch => {
  dispatch({
    type: USER_HAS_LOGIN,
    payload: token
  });
}

export const performLogin = (url, userData) => dispatch => {
  var csrftoken = Cookies.get('csrftoken');
  dispatch(beginUserLogin());
  fetch(url, {
    method: 'POST',
    headers: {
      'content-type': 'application/json',
      'X-CSRFToken': csrftoken
    },
    body: JSON.stringify(userData)
  })
  .then(res => res.json())
  .then(key => dispatch({type: USER_LOGIN_SUCCESS, payload:key}));
}

export const performLogout = (url) => dispatch => {
  fetch(url, {
    method: 'POST',
    headers: {
      'content-type': 'application/json'
    }
  })
  .then(dispatch({type: USER_LOGOUT_SUCCESS}));
}



