import React, { Component } from 'react';
import { connect } from 'react-redux';
import { performLogin, setUserToken, performLogout } from '../actions/userActions';
import PropTypes from 'prop-types';
import { LOGIN_URL, LOGOUT_URL, REGISTER_URL } from '../listrUrls';
import CSRFToken from './csrftoken';

class Account extends Component {

  constructor(props){
    super(props);
    this.onLogin = this.onLogin.bind(this);
    this.onRegister = this.onRegister.bind(this);
    this.onLogout = this.onLogout.bind(this);
    this.checkIfLoggedIn = this.checkIfLoggedIn.bind(this);
    this.state = {listrUsername: '', listrPassword: '', listrPassword2: ''};
  }
  componentDidMount(){
    this.checkIfLoggedIn();
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value})
  }

  checkIfLoggedIn(){
    var cachedToken = localStorage.getItem("token");
    if(cachedToken !== null && cachedToken !== undefined){
      this.props.setUserToken(cachedToken);
      return true;
    }
    return false;
  }

  onLogout(){
    localStorage.removeItem('token');
    this.props.performLogout(LOGOUT_URL);
  }

  onLogin(e){
    if(this.checkIfLoggedIn()){return;}
    e.preventDefault();

    const userData = {
      "username": this.state.listrUsername,
      "password": this.state.listrPassword
    }

    this.props.performLogin(LOGIN_URL, userData);
  }

  onRegister(e){
    if(this.checkIfLoggedIn()){return;}
    e.preventDefault();

    const userData = {
      "username": this.state.listrUsername,
      "password1": this.state.listrPassword,
      "password2": this.state.listrPassword2
    }

    this.props.performLogin(REGISTER_URL, userData);
  }

  render(){
    var button, signUpButton;
    const dropDownStyle={
      width: '250px',
    }
    if(this.props.isLoginningIn){
      button = <h3>Logging In</h3>
    }else if(this.props.userToken){
      button = <li class="nav-item dropdown">
                <a class="nav-link dropdown-toggle" href="" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    Logged In
                  </a>
                  <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
                    <a class="dropdown-item" href="" onClick={this.onLogout}>Log Out</a>
                  </div>
              </li>
    }else{
      signUpButton = (
              <li class="nav-item dropdown">
                <a class="nav-link dropdown-toggle" href="" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  Sign Up
                </a>
                <form class="dropdown-menu px-4" onSubmit={(e) => this.onRegister(e)} style={dropDownStyle}>
                  <h4>Make an Account</h4>
                  <div class="form-group">
                    <label for="listrUsername" class="bmd-label-floating">Username</label>
                    <input type="text" class="form-control" id="listrUsername" onChange={(e) => this.handleChange(e)}/>
                  </div>
                  <div class="form-group">
                    <label for="listrPassword" class="bmd-label-floating">Password</label>
                    <input type="password" class="form-control" id="listrPassword" onChange={(e) => this.handleChange(e)} />
                  </div>
                  <div class="form-group">
                    <label for="listrPassword" class="bmd-label-floating">Confirm Password</label>
                    <input type="password" class="form-control" id="listrPassword2" onChange={(e) => this.handleChange(e)} />
                  </div>
                  <button type="submit" class="btn btn-primary">Sign Up</button>
                </form>
              </li>);
      button = (
              <li class="nav-item dropdown">
                <a class="nav-link dropdown-toggle" href="" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  Login
                </a>
                <form onSubmit={(e) => this.onLogin(e)} class="dropdown-menu px-4" style={dropDownStyle}>
                  <h4>Login To Listr</h4>
                  <div class="form-group">
                    <label for="listrUsername" class="bmd-label-floating">Username</label>
                    <input type="text" class="form-control" id="listrUsername" onChange={(e) => this.handleChange(e)}/>
                  </div>
                  <div class="form-group">
                    <label for="listrPassword" class="bmd-label-floating">Password</label>
                    <input type="password" class="form-control" id="listrPassword" onChange={(e) => this.handleChange(e)} />
                  </div>
                  <button type="submit" class="btn btn-primary">Login</button>
                </form>
              </li>);
    }
    return(
      <ul class="navbar-nav">
        <li class="nav-item active">
          <a class="nav-link" href="">Home <span class="sr-only">(current)</span></a>
        </li>
        {signUpButton}
        {button}
      </ul>
    );
  }

}

Account.proptypes = {
  performLogin: PropTypes.func.isRequired,
  performLogout: PropTypes.func.isRequired,
  setUserToken: PropTypes.func.isRequired
}

const mapStateToProps = state => ({
  userToken: state.users.token,
  isLoginningIn: state.users.loading
});

export default connect(mapStateToProps, { performLogin, setUserToken, performLogout })(Account);



