import React, { Component } from 'react';
import "../css/CollabNames.css";
import { performListPost } from '../actions/listActions';
import { LISTS_URL } from '../listrUrls';
import { connect } from 'react-redux';
import PropTypes from 'prop-types'

class NewListForm extends Component {

  constructor(props){
    super(props);
    this.state = {
      collabs: [],
      listTitle: null
    }
    this.addItem = this.addItem.bind(this);
    this.listCollabs = this.listCollabs.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.createNewList = this.createNewList.bind(this);
  }

  // LIST COLLABORATORS
  deleteItem(key){
    var filteredItems = this.state.collabs.filter(function (user){
      return (user !== key);
    });
    this.setState({
      collabs:filteredItems
    });
  }
  addItem(e){
    if(this._inputElement.value !== ""){
      var newCollab = this._inputElement.value
      };

      this.setState((prevState) => {
        return {
          collabs: prevState.collabs.concat(newCollab)
        };
      });
      this._inputElement.value = "";
      e.preventDefault();
    }
  _handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      this.addItem(e);
    }
  }
  listCollabs(collab){
    return <li onClick={() => this.deleteItem(collab)} >@{collab}</li>
  }
  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value});
  }

  //LIST POSTING
  createNewList(e){
    e.preventDefault();
    const listData = {
      "name": this.state.listTitle,
      "static_id": this.makeid(),
      "collabs": this.state.collabs
    }
    console.log(this.state.collabs)
    this.props.performListPost(LISTS_URL, listData, this.props.token);
  }
  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

    for (var i = 0; i < 6; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
  }


  render () {
    var collabs = this.state.collabs.map(this.listCollabs);
    return(
      <div class="modal fade" id="newListModal" tabIndex="-1" role="dialog" aria-labelledby="newListModal" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">Create New List</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <form onSubmit={this.createNewList}>
                <div class="form-group">
                  <label htmlFor="list-name" class="col-form-label">Name Your List:</label>
                  <input type="text" class="form-control" autoComplete="off" id="listTitle" onChange={e => this.handleChange(e)} placeholder="List Name"/>
                </div>

                <label htmlFor="collabs" class="col-form-label">Add Some Friends:</label>
                <div class="input-group mb-2">
                  <div class="input-group-prepend">
                    <div class="input-group-text">@</div>
                  </div>
                  <input ref={(a) => this._inputElement = a} onKeyPress={this._handleKeyPress} type="text" class="form-control" id="collabs" placeholder="Username" />
                  <p class="helperText">You can add multiple users. Press 'Enter' after each username to add another.</p>
                </div>
                <div class="collabList">
                {collabs}
                </div>
                <button type="submit" data-toggle="modal" data-target="#newListModal" class="btn btn-primary">Create List</button>
              </form>

            </div>
          </div>
        </div>
      </div>
    );
  }

}


NewListForm.proptypes = {
  performListPost: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
});

export default connect(mapStateToProps, { performListPost })(NewListForm);



