import React, { Component } from 'react';
import Lists from './components/Lists';
import Navbar from './components/Navbar';
import NewListForm from './components/NewListForm';
import { LISTS_URL } from './listrUrls';
import { fetchLists } from './actions/listActions';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
class App extends Component {

  constructor(props){
    super(props);
    this.onFetchLists = this.onFetchLists.bind(this);
    this.state = { hasLoadedOnce: false }
  }

  onFetchLists(){
    this.props.fetchLists(LISTS_URL, this.props.token);
  }

  componentWillMount(){

  }

  render() {
    var data;
    var sharedData;
    var lists;
    var addButtonStyle = {
      display: 'none'
    };
    //USER LISTS
    if(this.props.isLoading){
      data = <p>Loading</p>
    }else if(this.props.error){
      data = <p>{this.props.error}</p>
    }
    if(this.props.token){
      if(this.state.hasLoadedOnce === false){
        this.props.fetchLists(LISTS_URL, this.props.token);
        this.setState({hasLoadedOnce: true});
      }
      if(this.props.lists){
        data = <Lists data={this.props.lists} key="user"/>
        addButtonStyle = {
          marginLeft: '10px',
          marginTop: '-15px'
        };
      }
      if(this.props.shared){
        sharedData = <Lists data={this.props.shared} key="shared" listType="shared"/>
      }
    }
    const headerStyle = {
      display: 'inline'
    };

    return (
      <div>
        <Navbar />
        <div class="container">
          <br />
          <header class="App-header">
            <h1 class="App-title" style={headerStyle}>My Lists</h1>
            <button class="btn btn-primary" data-toggle="modal" data-target="#newListModal" style={addButtonStyle}><i class="fa fa-plus"></i></button>
            <button class='btn btn-light' style={addButtonStyle} onClick={this.onFetchLists}><i class="fa fa-sync-alt"></i></button>
          </header>
          <div>
          {data}
          <br />
          <header class="App-header">
            <h1 class="App-title" style={headerStyle}>Shared With Me</h1>
          </header>
          {sharedData}
          </div>
          <NewListForm />
        </div>
      </div>
    );
  }
}

App.proptypes = {
  fetchLists: PropTypes.func.isRequired,
  lists: PropTypes.array.isRequired,
  shared: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
  lists: state.lists.data,
  error: state.lists.error,
  shared: state.lists.sharedData,
  isLoading: state.lists.loading,
});

export default connect(mapStateToProps, { fetchLists })(App);



